.App {
  text-align: center;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0;

}

.globalNavigation {
  display: flex;
  justify-content: space-around;
  padding: 25px 0px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 15pt;
  background-color: antiquewhite;
}

.globalNavigation > div{
  width: 800px;
  display: flex;
  justify-content: space-around;
}

.globalNavigation > div > a {
  color: rgb(141, 103, 61);
  display: flex;
}

.heroTekst {
  color: antiquewhite;
  font-size: 60px;
  position: absolute;
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  min-width: 60%;
}

.forsideBillede > img{
  width: 100%;
}

