.footer {
  background-color: antiquewhite;
  padding: 10px 0px;
  display: flex;
  justify-content: space-around;
  color: rgb(141, 103, 61);
}

.soMe > div {
  display: flex;
  justify-content: center;
}

.soMe > div > img {
  width: 50px;
  padding: 5px;
}

/* PRODUKTKORT */

.kortraekke {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.kort {
  position: relative;
  width: 240px;
  margin: 10px;
  box-shadow: 2px 2px 6px 0px rgba(0, 0, 0, 0.3);
}

.adminkort {
  cursor: pointer;
}

.kort img {
  max-width: 100%;
}

.kort .tekst {
  text-align: center;
  padding: 0 10px 50px;
}

.kort .tekst p:nth-of-type(1) {
  min-height: 65px;
}
    
.kort h3 {
  text-align: center;
  margin-bottom: 5px;
}

